@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.services_and_parts {
  background-image: url("../src/assets/images/services_hero.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.financingHero {
  background-image: url("../src/assets/images/sany-retroexcavadora.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.tab {
  overflow: hidden;
}

.tab-label .test .minus {
  display: none;
}

.tab-content {
  max-height: 0;
  transition: all 0.3s;
}

input:checked+.tab-label .test {
  background-color: #000;
}

input:checked+.tab-label .test .minus {
  display: block;
  transform: rotate(180deg);
  stroke: #fff;
}

input:checked+.tab-label .test .plus {
  display: none;
}

input:checked+.tab-label::after {
  transform: rotate(60deg);
}

input:checked~.tab-content {
  max-height: 100vh;
}

.h-iso-gray {
  background-image: url("./assets/icons/iso_gray.svg");
  background-size: 500px;
  background-repeat: no-repeat;
  background-position: left;
}

.bg-t {
  background-color: transparent;
}

.bg-hero{
  background-image: url("../src/assets/images/hero_background.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.none-scroll-custom::-webkit-scrollbar {
  display: none;
}